#profile {
  margin-top: 50px;
  background: rgb(228,228,228);
  background: -webkit-linear-gradient(315deg, rgba(228,228,228,1) 0%, rgba(221,221,221,1) 57%, rgba(181,181,181,1) 100%);
  background: -o-linear-gradient(315deg, rgba(228,228,228,1) 0%, rgba(221,221,221,1) 57%, rgba(181,181,181,1) 100%);
  background: linear-gradient(135deg, rgba(228,228,228,1) 0%, rgba(221,221,221,1) 57%, rgba(181,181,181,1) 100%);
  margin-left: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 800px;
  height: 400px;
}

#profile-left {
  height: 400px;
  width: 300px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  border-right: 1px solid black;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
}

#profile-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
}

#profile-avatar {
  display: block;
  margin-top: 30px;
  margin-bottom: 10px;
  height: 200px;
  /* border-radius: 50%; */
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

#profile-avatar:hover {
  -webkit-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.85);
          box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.85);
  border-radius: 50%;
  -webkit-transform: rotate(720deg);
      -ms-transform: rotate(720deg);
          transform: rotate(720deg);
}

#profile-icon-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#profile-icon-links a {
  display: block;
  width: 40px;
  line-height: 40px;
  margin: 5px;
}

#profile-icon-links img {
  width: 30px;
}

#profile-icon-links img:hover {
  -webkit-filter: drop-shadow( 0px 0px 4px rgba(0,0,0,0.7));
          filter: drop-shadow( 0px 0px 4px rgba(0,0,0,0.7));
}

#profile-right-inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-item-align: center;
      align-self: center;
  height: 150px;
}

#profile-right-inner div {
  text-align: left;
}

#profile-right-inner a {
  text-decoration: none;
  color: #5E5E5E;
}

#profile-right-description {
  -ms-flex-pack: center;
      justify-content: center;
  width: 400px;
  padding: 7px;
  margin: 40px 40px 0px;
  word-break: break-all;
}



