#page-author {
  margin-top: 50px;
  padding-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 90vh;
}

#author-title {
  font-size: 110px;
  width: 1000px;
  font-weight: bolder;
  color: #464600;
  text-align: center;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

#author-title:hover {
  -webkit-filter: drop-shadow(0 0 20px white);
          filter: drop-shadow(0 0 20px white);
}

#author-subtitle {
  margin-top: 50px;
  font-size: 50px;
  font-weight: bold;
  color: #505050;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  -webkit-animation-name: subtitle;
          animation-name: subtitle;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  opacity: 0;
}

#author-subtitle:hover {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes subtitle {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  } to {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
    opacity: 0;
  }
}

@keyframes subtitle {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  } to {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
    opacity: 0;
  }
}

#author-aboutme a {
  text-decoration: none;
}

#aboutme-button {
  margin: 70px;
  color: #FFFFFF;
  text-decoration: none;
  text-align: center;
  width: 90px;
  height: 40px;
  line-height: 40px;
  border: 3px solid #3393FF;
  border-radius: 10px;
  background: rgb(121,139,255);
  background: -webkit-radial-gradient(circle, rgba(121,139,255,1) 0%, rgba(80,86,207,1) 57%, rgba(43,75,223,1) 100%);
  background: -o-radial-gradient(circle, rgba(121,139,255,1) 0%, rgba(80,86,207,1) 57%, rgba(43,75,223,1) 100%);
  background: radial-gradient(circle, rgba(121,139,255,1) 0%, rgba(80,86,207,1) 57%, rgba(43,75,223,1) 100%);
}

#aboutme-button:hover {
  background: rgba(121,139,255,1);
  background: -webkit-radial-gradient(circle, rgba(43,75,223,1) 0%, rgba(80,86,207,1) 57%, rgba(121,139,255,1) 100%);
  background: -o-radial-gradient(circle, rgba(43,75,223,1) 0%, rgba(80,86,207,1) 57%, rgba(121,139,255,1) 100%);
  background: radial-gradient(circle, rgba(43,75,223,1) 0%, rgba(80,86,207,1) 57%, rgba(121,139,255,1) 100%);
  -webkit-box-shadow: 0 0 10px 2px #FFFFFF;
          box-shadow: 0 0 10px 2px #FFFFFF
}

#aboutme-button:active {
  background: #4C7FBA;
}

#author-line {
    border: 0;
    margin-top: 40px;
    color: #15009F;
    height: .5em;
    white-space: nowrap;
    letter-spacing: 40vw;
    padding-top: .5em;
}
#author-line::before {
    content: '\2000\2000';
    text-decoration: overline;
    -webkit-text-decoration: overline wavy;
            text-decoration: overline wavy;
}



